  import IKUtils from 'innerken-js-utils'
  import { IkDataTable } from 'metaflow-js'
  import { formatDate } from '@/common/common'
  import Utils from '../../components/Utlis/Utils'
  import Storno from '../../model/Storno'
  import { discountHeaders, stornHeaders, umsatzHeaders } from '@/model/Dashboard'

  export default {
    name: 'DashboardDashboard',
    components: {
      IkDataTable,
    },
    data () {
      return {
        storno: Storno,
        stornoDialog: false,
        items: [
          this.$i18n.t('销售数据统计'),
          this.$i18n.t('跑堂排行')],
        salesDataChart: {
          options: {
            low: 0, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
            chartPadding: {
              top: 0,
              right: 0,
              bottom: 0,
              left: 0,
            },
          },
        },
        servantHeader: [
          {
            // text: 'Name',
            text: this.$i18n.t('servant'),
            value: 'name',
          },
          {
            text: this.$i18n.t('income'),
            value: 'income',
            align: 'right',
          },
          {
            text: this.$i18n.t('billCount'),
            value: 'billCount',
            align: 'right',
          },
        ],
        dishesHeader: [
          {
            text: this.$i18n.t('Ranking'),
            value: 'rank',
          },
          {
            text: this.$i18n.t('image'),
            value: 'photo',
            align: 'center',
          },
          {
            text: this.$i18n.t('name'),
            value: 'name',
            align: 'right',
          },
          {
            text: this.$i18n.t('price'),
            value: 'price',
            align: 'right',
          },
          {
            text: this.$i18n.t('count'),
            value: 'sumCount',
            align: 'right',
          },
        ],
        indexData: {
          sevenDaysFoodDrinkIncome: [],
          thisMonthServantRank: [],
          realTimeIncomeList: {
            taxInfos: [],
          },
          todayInfo: {
            sumIncome: '0',
          },
          tableUsageStatus: {
            inUse: '0',
          },
          thisMonthAverageTablesRenewRate: 0,
        },
        restaurantName: '',
        dishIncomeList: [],
        dishIncomeWithPLog: [],
        cashAmount: 0,
        cardAmount: 0,
        creditCardAmount: 0,
        taxItem: {
          consumeTypeName: '',
          taxGroupTotal: 0,
          taxRateGroupValue: 0,
        },
        paymentMethodList: [],
        umsatzHeaders: umsatzHeaders(),
        discountHeaders: discountHeaders(),
        stornHeaders: stornHeaders(),
        showTotalPayMentSum: true,
        flag: null,

      }
    },

    computed: {
      realTimeIncomeList () {
        if (this.indexData.realtimeIncomeAndInfo) {
          return this.indexData.realtimeIncomeAndInfo
        } else {
          return undefined
        }
      },
      taxInfos () {
        if (this.indexData.realtimeIncomeAndInfo.taxInfos) {
          const taxItemArr = []
          this.indexData.realtimeIncomeAndInfo.taxInfos.forEach(item => {
            this.taxItem = IKUtils.deepCopy(item)
            taxItemArr.push(this.taxItem)
          })
          return taxItemArr
        } else {
          return undefined
        }
      },
      servantImgRoot () {
        return this.$store.getters.rootUrl + GlobalSettings.servantRoot
      },
      dishImgRoot () {
        return this.$store.getters.rootUrl + GlobalSettings.dishImageRoot
      },
      salesData () {
        const labels = []
        const foodData = []
        const drinkData = []
        for (const i of this.indexData.sevenDaysFoodDrinkIncome) {
          const food = i.find(d => d.name.toLowerCase() === 'foods')
          const drink = i.find(d => d.name.toLowerCase() === 'drinks')
          labels.push(food.dateDisplay)
          foodData.push(food.income)
          drinkData.push(drink.income)
        }
        return {
          labels: labels.reverse(),
          series: [foodData.reverse(), drinkData.reverse()],
        }
      },
      unpaidTable () {
        if (this.indexData.realtimeIncomeAndInfo) {
          return this.indexData.realtimeIncomeAndInfo.unpaidTable
        } else {
          return undefined
        }
      },
      stornoList () {
        if (this.indexData.realtimeIncomeAndInfo) {
          return this.indexData.realtimeIncomeAndInfo.storno
        } else {
          return undefined
        }
      },
      stornoAmount () {
        if (this.indexData.realtimeIncomeAndInfo.storno) {
          const stornoArr = this.indexData.realtimeIncomeAndInfo.storno
          return stornoArr.reduce((arr, item) => arr + parseFloat(item.price), 0)
        } else {
          return undefined
        }
      },
      thisMonthServantRank () {
        return this.indexData.thisMonthServantRank
      },
      todayInfo () {
        return this.indexData.todayInfo
      },
      tableUsageStatus () {
        return this.indexData.tableUsageStatus
      },
      tableRenew () {
        return this.indexData.thisMonthAverageTablesRenewRate
      },
      thisWeekMostPopularDishes () {
        return this.indexData.thisWeekMostPopularDishes
      },
      totalSales () {
        return this.sales.reduce((acc, val) => acc + val.salesInM, 0)
      },
    },
    created () {
      this.onceData()
      this.reloadData()
    },
    methods: {
      checkStorno () {
        this.stornoDialog = true
      },
      changeDateToDay () {
        this.dishIncomeList = this.indexData.thisDayIncomePieChart
      },
      changeDateToMonth () {
        this.dishIncomeList = this.indexData.thisMonthIncomePieChart
      },
      async reprint (item) {
        await this.model.reprint(item)
        IKUtils.toast()
      },
      toZbon () {
        this.$router.push({ path: '/Bills/zbonlist' })
      },
      toAllBills () {
        this.$router.push({ path: '/Bills/history-bills' })
      },
      addDishes () {
        this.$router.push({ path: '/dishes/dishes-manage' })
      },
      addCategories () {
        this.$router.push({ path: '/dishes/labels-manage' })
      },
      dishStatistic () {
        this.$router.push({ path: '/dishes/dish-statistics' })
      },
      dailyBill () {
        this.$router.push({ path: '/Bills/today-bills' })
      },
      printTodayBills () {
        const dates = [formatDate(0), formatDate(0)]
        const filter = { timespan: dates ? dates.join(' - ') : '' }
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie alle Datensätze drucken?', () => {
          printSummaryBon(filter).then(() => {
            Utils.toast('Erfolgreich drucken!')
          })
        })
      },
      onceData () {
        getRestaurantInfo().then(res => {
          let arr = []
          arr = res.content[0].name.split('<BR>')
          this.restaurantName = arr.join(' ')
        })
      },
      calculateAmount (Array) {
        this.cashAmount = 0
        this.cardAmount = 0
        this.creditCardAmount = 0
        Array.forEach(item => {
          if (item.payLogs !== null) {
            item.payLogs.forEach(payItem => {
              if (payItem.paymentMethodId === '2') {
                this.cardAmount += parseFloat(payItem.amount)
              }
              if (payItem.paymentMethodId === '1') {
                this.cashAmount += parseFloat(payItem.amount)
              }
              if (payItem.paymentMethodId === '3') {
                this.creditCardAmount += parseFloat(payItem.amount)
              }
            })
          } else {
            if (item.payMethod === '3') {
              this.creditCardAmount += parseFloat(item.totalPrice)
            }
            if (item.payMethod === '2') {
              this.cardAmount += parseFloat(item.totalPrice)
            }
            if (item.payMethod === '1') {
              this.cashAmount += parseFloat(item.totalPrice)
            }
          }
        })
      },
      async reloadData () {
        await getIndexData().then(res => {
          this.indexData = res
          this.dishIncomeList = this.indexData.thisMonthIncomePieChart
        })
        const timeSpan = [formatDate(0), formatDate(0)].join(' - ')
        await ShowTodayBills(timeSpan).then(res => {
          this.dishIncomeWithPLog = res.content
          this.calculateAmount(this.dishIncomeWithPLog)
        })
      },
      beforeDestroy () {
        clearTimeout(this.timer1)
        clearTimeout(this.timer2)
        this.timer1 = null
        this.timer2 = null
      },
      complete (index) {
        this.list[index] = !this.list[index]
      },
    },
  }
</script>

